import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PortfolioaddComponent } from './portfolioadd/portfolioadd.component';
import { PortfoliolistComponent } from './portfoliolist/portfoliolist.component';

const routes: Routes = [
  {
    path: '', 
    children: [
        { path: '', component: PortfoliolistComponent },
        { path: 'add', component: PortfolioaddComponent },
        { path: 'add/:id', component: PortfolioaddComponent }
    ]
}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PortfolioRoutingModule { }
