import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountService, AlertService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-portfolioadd',
  templateUrl: './portfolioadd.component.html',
  styleUrls: ['./portfolioadd.component.less']
})
export class PortfolioaddComponent implements OnInit {

  form: FormGroup;
  id: string;
  loading = false;
  submitted = false;
  portfolioId: string;
  categoryData: any;
  blogFile: File = null; // Variable to store file
  imageSrc: string;
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
    private alertService: AlertService
  ) { }

  add = new FormGroup({
    title: new FormControl('',[Validators.required]),
    description: new FormControl('',[Validators.required]),
    image: new FormControl('',[Validators.required]),
    url: new FormControl('',[Validators.required]),
    sortOrder: new FormControl('',[Validators.required]),
    isActive: new FormControl('',[Validators.required]),
    categoryId: new FormControl(''),
  })

  ngOnInit() {
    console.warn(this.route.snapshot.params.id);
    this.accountService.PortfolioById(this.route.snapshot.params.id).subscribe((result: any) => {
      console.warn(result);
      debugger;
      this.add = new FormGroup({
        portfolioId: new FormControl(result['portfolioId']),
        title: new FormControl(result['title']),
        description: new FormControl(result['description']),
        image: new FormControl(result['image']),
        url: new FormControl(result['url']),
        categoryId: new FormControl(result['categoryId']),
        sortOrder: new FormControl(result['sortOrder']),
        isActive: new FormControl(result['isActive']),
      })
    })
    this.accountService.BlogCategorygetAll1().subscribe((categoryData) => {
      console.warn(categoryData);
      this.categoryData = categoryData;
      // this.TagTempdata = this.TagData;
    })
  }
  // convenience getter for easy access to form fields
  get f() { return this.add.controls; }

  onPortfolioFileChange(event) {
    this.blogFile = event.target.files[0];
    debugger;
        const reader = new FileReader();

  //   if (event.target.files && event.target.files.length) {
  //     const [file] = event.target.files;
  //     reader.readAsDataURL(file);

  //     reader.onload = () => {

  //       this.imageSrc = reader.result as string;

  //       this.add.patchValue({
  //         fileSource: reader.result
  //       });

  //     };
  // }
}
  onSubmit() {
    this.submitted = true;
    console.warn(this.add.value)
    // reset alerts on submit
    this.alertService.clear();
    // stop here if form is invalid
    if (this.add.invalid) {
      return;
    }
    if (this.add.value.portfolioId == "" || this.add.value.portfolioId == "0" || this.add.value.portfolioId == null) {
      console.warn(this.add.value)
      this.loading = true;
      debugger
      this.accountService.AddPortfolioData(this.add.value,this.blogFile)
        .pipe(first())
        .subscribe({
          next: () => {
            debugger
            this.alertService.success('Portfolio added successfully', { keepAfterRouteChange: true });
            this.router.navigate(['/admin/portfolio'], { relativeTo: this.route });
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
    }
    else {
      console.warn(this.add.value)
      this.loading = true;
      this.accountService.UpdatePortfolioData(this.route.snapshot.params.id, this.add.value,this.blogFile)
        .pipe(first())
        .subscribe({
          next: () => {
            this.alertService.success('Portfolio Update successfully', { keepAfterRouteChange: true });
            this.router.navigate(['/admin/portfolio'], { relativeTo: this.route });
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
    }
    Swal.fire(
      'Done!',
      'Your record has been successfully added',
      'success'
    )
  }
  // imageSrc: string;
  // onFileChange(event) {
  //   const reader = new FileReader();
  //   if (event.target.files && event.target.files.length) {
  //     const [file] = event.target.files;
  //     reader.readAsDataURL(file);
  //     reader.onload = () => {
  //       this.imageSrc = reader.result as string;
  //       this.add.patchValue({
  //         fileSource: reader.result
  //       });
  //     };
  //   }
  // }


}
